<template>
  <div class="login">
    <el-row>
      <el-col :span="6" class="left"><div class="lg-left"></div></el-col>
      <el-col :span="12" class="middle">
        <div class="lg-logo">
          <h1>智慧学工系统</h1>
        </div>
        <div class="login-inner">
          <p>您尚未登录，请登录</p>
          <el-form :model="user" :rules="rules" ref="user">
            <el-form-item prop="nickname">
              <el-input size="large" type="text" v-model="user.nickname" auto-complete="off" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input size="large" ref="pass" type="password" v-model="user.password" auto-complete="off" placeholder="请输入密码"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button class="w100s" :loading="load.tag" size="large" type="primary" @click="login('user')">{{load.name}}</el-button>
            </el-form-item>
          </el-form>
          <div class="el-upload__tip">提示：因智慧学工系统已与学校统一信息门户对接，若身份证号含有字母，请以小写形式登入！</div>
        </div>
      </el-col>
      <el-col :span="6" class="right"><div class="lg-right fr"></div></el-col>
    </el-row>
  </div>
</template>
<script>
  export default {
    name: 'login',
    data () {
      var usernameRule = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用户名'))
        } else {
          callback()
        }
      }
      var passwordRule = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'))
        } else if (value !== '') {
          var reg = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d]{6,18}$/
          reg.test(value) ? callback() : callback(new Error('请输入由字母数字组成的6-18位密码'))
        } else {
          callback()
        }
      }
      return {
        user: {
          nickname: '',
          password: ''
        },
        load: {
          tag: false,
          name: '登 录'
        },
        rules: {
          nickname: [
            { validator: usernameRule, trigger: 'change' }
          ],
          password: [
            { validator: passwordRule, trigger: 'change' }
          ]
        }
      }
    },
    created () {
//      this.getUser()
      this.loginUser ()
    },
    methods: {
      loginUser () {
        let self = this
        this.$axios.get("../sso/loginUser").then(function(res){
          if(res.data){
            self.$router.push({path: 'home'})
          }
        })
      //   this.$axios.get("./user/api/newLogin").then(function(res){
      //     if(res.data){
      //         localStorage.setItem("login",true)
      //         self.$router.push({
      //           path:'/home',
      //           query:{data:res.data.data.name}
      //         })
      //     }else{
      //         self.$router.push("http://172.16.16.102/zfca")
      //     }
      // })
      },
      login () {
        let self = this
        self.$refs['user'].validate((valid) => {
          if (valid) {
            self.load.tag = true
            self.load.name = '正在登录'
            let loginData = {
              username: self.user.nickname,
              password: self.user.password
            }
            self.$axios.post("../sso/login", loginData).then(function(res){
              self.load.tag = false
              self.load.name = '登 录'
              if(res.data){
//                console.log(res.data)
//                localStorage.setItem('loginData', JSON.stringify(res.data.data))
                self.$router.push({path: 'home'})
              }else{
                self.$message({
                  message: "用户名或密码错误",
                  type: 'error'
                })
                self.$refs.pass.focus()
              }
            })
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .login {
    width: 100%;
    height: 100%;
    min-width: 1280px;
    .lg-left {
      width: 431px;
      height: 585px;
      background-image: url("../assets/login-left.png");
    }
    .lg-right {
      width: 374px;
      height: 502px;
      background-image: url("../assets/login-right.png");
    }
    .lg-logo {
      width: 200px;
      height: 50px;
      margin: 50px auto 200px auto;
      background-image: url("../assets/login-logo.png");
      h1 {
        width: 200px;
        display: inline-block;
        margin-top: 80px;
        text-align: center;
        color: #12a1e9;
        font-family: 华文行楷;
      }
    }
    &-inner {
      width: 320px;
      margin: 0 auto;
