        }
      }
      .middle {
        width: 25%;
      }
      .right {
        width: 35%;
      }
      &-inner {
        width: 240px;
      }
    }
  }
  @media screen and (max-width: 768px) {
    // 690-378
    .login {
      width: 100%;
      height: 100%;
      min-width: 378px;
      .lg-left {
        display:none
      }
      .lg-right {
        display:none
      }
      .lg-logo {
        width: 200px;
        height: 50px;
        margin: 50px auto 100px auto;
        display: block;
        background-image: url("../assets/login-logo.png");
        h1 {
          width: 200px;
          display: inline-block;
          margin-top: 60px;
          text-align: center;
          font-size: 20px;
        }
      }
      .el-col-12 {
        width: 100%;
        padding: 20px;
      }
      .login-inner {
        width: 200px;
        p {
          margin-bottom: 20px;
        }
        button {
          width: 100%
        }
      }
    }
  }
  @media screen and (max-width: 420px) {
    // 小于378
    .login {
      max-width: 378px;
    }
  }
  @media screen and (max-width: 320px) {
    .login {
      min-width: 320px;
      .lg-logo {
        margin: 50px auto 70px auto;
      }
    }
  }

  // @media screen and (max-width: 500px) {
