<template>
  <div class="home">
    <div class="contain">
      <div class="title">
        <div class="title_con">
          <img src="./../assets/logo.png" alt="">
          <div class="logo">
            <p>浙江工业大学</p>
            <p>智慧学工平台</p>
          </div>
          <span class="title_right" @click="exit()" v-loading="outLoading"><img src="../../static/assets/header.png" alt="" style="width:15px;height:15px;margin-right:2px">{{name}}</span>
        </div>
      </div>
      <div class="main">
        <div class="slide" style="margin-bottom: 40px">
          <el-carousel :interval="5000" arrow="always" height="180px">
            <el-carousel-item v-for="item in 4" :key="item">
              <img src="../assets/banner.png"></img>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="kind" style="display:none">
          <div>当前分类共<span>{{num}}</span>个</div>
          <div>分类筛选:<span :class="{active: isActive}">全部</span></div>
        </div>

        <div class="item" v-for="app in appList" @click="enterApp(app)">
          <span class="cover fl" :style="{'background-color': app.backColor}" style="text-align: center"><img style="margin-top: 5px;" :src="pic+app.picUrl+'.png'" alt=""></span>
          <h3 class="app_title">{{app.name}}</h3>
          <p class="app_type">{{app.type}}</p>
          <p class="app_detail">{{app.description}}</p>
        </div>
        <div class="item" style="text-align: center" v-loading="loading">
          <img src="../../static/assets/more.png" alt="">
          <p style="color:#7f7f7f">敬请期待</p>
        </div>
      </div>
      <el-dialog
        title="您有多种角色，请选择其中一个"
        :visible.sync="dialogFormVisible"
        width='400px'
        :show-close="shouClose"
        :before-close="handleClose">
        <el-form :model="form">
          <el-form-item label="选择角色">
            <el-select v-model="form.role" placeholder="请选择要登录的角色">
              <el-option :label="item.name" :key="item.id" :value="item.id" v-for="item in roleList"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="subRole()">确 定</el-button>
        </div>
      </el-dialog>
      <div class="foot">
        <p>浙江工业大学&nbsp;&nbsp;|&nbsp;&nbsp;浙江工业大学本科招生网</p>
        <p>浙江工业大学 & 成都寻道科技有限公司 联合研制 版权所有&copy;2018</p>
        <p>地址 : 浙江省杭州市下湖区潮王路18号&nbsp; 邮编 : 310014 查号台 : 0571-88320032, 0571-85290885</p>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
  * {box-sizing:border-box;}
  .home {
    background: #F9FAFC;
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: auto;
    background:url('../assets/bg.png') no-repeat;
    background-size:cover;
  }
  .contain{
    width:1120px;
    height:95%;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    margin:auto;

  }
  .title {
    width: 100%;
    height: 60px;
    padding:10px;
    background:rgba(241,246,251,0.8);
    .title_con {
      position: relative;
      width: 1100px;
      height: 100%;
      margin: 0 auto;
    }
    .title_right {
      position: absolute;
      right: 10px;
      top: 50%;
      margin-top: -10px;
      color: black;
      font-size: 14px;
      cursor: pointer;
    }
    .logo{
      float:left;
      margin-left:10px;
    }
    img {
      margin-top: 2px;
      float:left;
    }
    p {
      color: #12A1E9;
      font-size:14px;
      font-weight:600;
    }
  }
  .main {
    width: 1120px;
    min-height: calc(~"100% - 190px");
    overflow: auto;
    box-sizing: border-box;
    background:rgba(241,246,251,0.8);
    padding:0 10px;
    .kind {
      width: 100%;
      height: 100px;
      background-color: #fff;
      margin: 20px 0;
      box-shadow: 0 2px 6px rgba(139,157,175,.1);
      span {
        color: #12A1E9;
        margin: 0 2px;
        font-size: 16px;
      }
      div {
        width: 100%;
        height: 50px;
        font-size: 14px;
        line-height: 50px;
        padding-left: 10px;
        box-sizing: border-box;
      }
      div:first-child {
        border-bottom: 1px solid #F9FAFC;
      }
      .active {
        color: #12A1E9;
        font-weight: 600;
        font-size: 14px;
        margin: 0 10px;
      }
    }
  }
  .item {
    float: left;
    width: 260px;
    height: 140px;
    margin-right: 20px;
    margin-bottom: 20px;
    background-color: #fff;
    padding: 15px;
    box-shadow: 0 2px 6px rgba(139,157,175,.1);
    cursor: pointer;
    position: relative;
    box-sizing:border-box;
    span {
      float: left;
      display: inline-block;
      width: 50px;
      height: 50px;
    }
    .app_title {
      margin-top: 3px;
      margin-left: 60px;
      font-size: 16px;
      font-weight: 400;
    }
    .app_type {
      margin-top: 10px;
      margin-left: 60px;
      font-size: 12px;
      color: #666;
    }
    .app_detail {
      margin-top: 15px;
      height: 54px;
      overflow: hidden;
      color: #819cb2;
      font-size: 12px;
    }
  }
  .item:nth-child(4n+2) {
    margin-right: 0;
  }
  .foot {
    width: 100%;
    height: 110px;
    background-color: rgba(245,250,252,0.8);
    text-align: center;
    font-size: 12px;
    padding: 10px 0;
    color: #767685;
    p:first-child {
      margin: 10px 0;
    }
  }
</style>
<script>
  export default {
    data () {
      return {
        name: this.$route.query.data,
        num: '',
        isActive: true,
        appList: [],
        pic: './static/assets/'+'cover-',
        dialogFormVisible: false,
        form: {
          role: ''
        },
        roleList: [],
        subData: {},
        shouClose: false,
        colors: [
          '#FF7500',
          '#22CDF6',
          '#01AAFF',
          '#FCC62E',
          '#FF7400',
          '#FF3D01',
          '#FA7801',
          '#FE4100',
          '#4F1CE1'
        ],
        loading: false,
        outLoading: false,
        sn: '',
        userType: ''
      }
    },
    mounted () {
      this.loginNew()
      //this.getAppAll()
    },
    methods: {
      handleClose() {
        this.dialogFormVisible = true
      },
      subRole () {
        if (!this.form.role) {
          this.$message({
            message: '请先选择角色',
            type: 'warning'
          })
        } else {
          var datas = this.subData.roleList
          for(var len = datas.length - 1; len>=0; len--) {
            if(datas[len].id !== this.form.role) {
              datas.splice(len, 1)
            }
          }
          this.dialogFormVisible = false
          this.getAppAll(this.subData)
          if (this.form.role === 1) {
            this.$axios.get(`./user/api/information/${this.sn}/学生`).then(res => {
              if (res.status) {
                let data = res.data.data
                 if (data.isUpdate === '1' && data.url) {
                  // self.$router.push(`${data.url}`)
                   // self.url = 'http://zjutzhxg.xdbigdata.com:17080/information-management/'
                  window.location.href = data.url
                }
              }
            })
          }
        }
      },
      getAppAll (param) {
        var self = this
        self.loading = true
        this.$axios.post('./product/api/findAll', param).then(function(res) {
          self.loading = false
          self.num = res.data.data.length
          self.appList = res.data.data
          for( var i=0,len = self.appList.length; i<len; i++) {
            self.appList[i].backColor = self.colors[i]
          }
          self.num = 10
        }).catch(function(error){
          console.log(error)
        })
      },
      enterApp (app) {
        window.open(`http://${app.address}`)
      },
      exit () {
        let self = this
        self.outLoading = true
        this.$axios.get("./user/api/out?date=" + Date.parse(new Date())).then(function(res){
          //location.href = res.data.data
          self.outLoading = false
          self.$message({
            message: '已登出',
            type: 'success'
          })
          self.$router.push({path: '/'})
        })
      },
      loginNew(){
//        let loginData = JSON.parse(localStorage.getItem('loginData'))
//        this.name = loginData.name
//        if(loginData.roleList.length > 1) {
//          this.dialogFormVisible = true
//          this.roleList = loginData.roleList
//          this.subData = loginData
//        } else {
//          this.getAppAll(loginData)
//        }
        var self = this
        this.$axios.get("./user/api/newLogin?date=" + Date.parse(new Date())).then(function(res){
          if(res.data){
            let data = res.data.data
            self.sn = data.sn
            self.userType = data.roleList
            self.name = data.name
            if(data.roleList.length > 1) {
              self.dialogFormVisible = true
              self.roleList = data.roleList
              self.subData = data
            } else {
              self.getAppAll(data)
            }
            return {
              sn: self.sn,
              userType: self.userType
            }
          }else{
            self.$router.push("http://172.16.16.102/zfca")
          }
        }).then(data =>{
          let userType = data.userType.filter(item => {
                if (item.name === '学生') {
                  return item
                }
              })[0].name
          if (data.userType.length === 1 && data.userType.length) {
            if(userType === '学生') {
              this.$axios.get(`./user/api/information/${data.sn}/${userType}`).then(res => {
              if (res.status) {
                let data = res.data.data
                if (data.isUpdate === '1' && data.url) {
                  // self.$router.push(`${data.url}`)
                  // self.url = 'http://zjutzhxg.xdbigdata.com:17080/information-management/'
                  window.location.href = data.url
                }
              }
            }) 
          } 
          } 
        })
      }
    }
  }
</script>



