import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/index'
import login from '@/components/login'

Vue.use(Router)

const router =new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/home',
      name: 'home',
      component: home
    }
  ]
})
export default router



